html, body {
  position: relative;
  top: 0;
  left: 0;
  width: 100vw;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-size: 16px;
}

body {
  background-color: #000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-thumb {
  background: #1976d2;
  border-radius: 2px;
}

::-webkit-scrollbar-track {
  background-color: #333;
  border-radius: 2px;
}